import { Component, OnInit } from '@angular/core';
import { EmployeListService } from 'src/app/appService/employe-list.service';

@Component({
  selector: 'app-crud',
  templateUrl: './crud.component.html',
  styleUrls: ['./crud.component.css'],
})
export class CrudComponent implements OnInit {
  Itemarr: any = [];
  fetching = true;
  constructor(private _employList: EmployeListService) {}

  ngOnInit(): void {
    this.Fetch();
  }

  onAdd(id, name, salary) {
    this.Itemarr.push({ id, name, salary });
    //console.log(this.Itemarr);
  }
  onDelete(id) {
    this.Itemarr.splice(id, 1);
    this.save();
  }
  save() {
    this._employList.saveData(this.Itemarr).subscribe(
      (response) => console.log(response),
      (err) => console.log(err)
    );
  }

  Fetch() {
    this._employList.FetchData().subscribe(
      (response) => {
        const data = JSON.stringify(response);
        console.log(data);
        this.Itemarr = JSON.parse(data);
        this.fetching = false;
      },
      (err) => console.log(err)
    );
  }
}
